* {
  margin: 0;
}

html,
body,
#background {
  background-color: #A3CEF1;
}

.App {
  display: flex;
  height: 100%;
  width: 100vw;
  max-width: 100%;
}

.App h1 {
  margin-bottom: 16px;
}

#root {
  height: 100%;
}

.Page-Header {
  font-size: 4rem;
}

.title-bar-div {
  display: flex;
  justify-content: space-between;
}

.Title-Container {
  display: flex;
  justify-content: space-between;
}

.Page-Title {
  display: flex;
  justify-content: flex-start;
}

.New-Country {
  position: 'absolute';
  top: '50%';
  left: '50%';
  transform: 'translate(-50%, -50%)';
  width: 400;
  background-color: 'background.paper';
  border: '2px solid #000';
  box-shadow: 24;
  padding: 4;
}

.loginLink, .logout {
  color: #fff;
  position:absolute;
  top: 8px;
  left: 8px;
}

.logoutLink {
  color: #fff;
}

 .logoutButton {
  display: inline-flex;
  flex-grow: 0;
  font-size: 15px;
  font-weight: 500;
  line-height: 18px;
  border-radius: 16px;
  height: 28px;
  min-width: 50px;
  width: auto;
  padding: 0 16px;
  margin-top: 25px;
}

.loginButton {
  display: inline-flex;
  flex-grow: 0;
  font-size: 15px;
  font-weight: 500;
  line-height: 18px;
  border-radius: 16px;
  height: 28px;
  min-width: 50px;
  width: auto;
  padding: 0 16px;
}

.loginForm {
  border: 1px solid #999;
  background-color: #dedede;
  padding: 8px;
  border-radius: 4px;
  position: absolute;
  width: 300px;
  top: 65px;
  left: 8px;
}